span.icon-x {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  border: 0.0625rem none;
  border-color: #c0c0c0 #9a9a9a #9a9a9a #c0c0c0;
  border-radius: 0.25rem;
  font-size: 1rem;
  background-color: inherit;
  color: inherit;
}
span.icon-x.right-angled {
  border-radius: 0;
}
span.icon-x.solid {
  border-style: solid;
}
span.icon-x.dotted {
  border-style: dotted;
}
span.icon-x.dashed {
  border-style: dashed;
}
span.icon-x.primary {
  background-color: #007bff;
}
span.icon-x.secondary {
  background-color: #6c757d;
}
span.icon-x.info {
  background-color: #17a2b8;
}
span.icon-x.success {
  background-color: #28a745;
}
span.icon-x.warning {
  background-color: #ffc107;
}
span.icon-x.error {
  background-color: #dc3544;
}
span.icon-x.dark {
  background-color: #343a40;
}
span.icon-x.light {
  background-color: #f8f9fa;
}
