div.button-group-x {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  border: none;
  border-color: #c0c0c0 #9a9a9a #9a9a9a #c0c0c0;
  border-radius: 0.25rem;
  font-size: 1rem;
  background-color: inherit;
  color: inherit;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
  align-items: center;
  vertical-align: bottom;
}
div.button-group-x > button.button-x:not(:disabled):not([data-disabled]):focus {
  z-index: 1;
}
div.button-group-x > button.button-x:not(:disabled):not([data-disabled]):hover {
  z-index: 2;
}
div.button-group-x[data-vertical] {
  flex-direction: column;
}
div.button-group-x[data-vertical] > button.button-x:not(.circle):not(.square):not(.right-angled):first-of-type:not(:last-of-type) {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
div.button-group-x[data-vertical] > button.button-x:not(.circle):not(.square):not(.right-angled):last-of-type:not(:first-of-type) {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
div.button-group-x[data-vertical] > button.button-x:not(.circle):not(.square):not(.right-angled):not(:first-of-type):not(:last-of-type) {
  border-radius: 0;
}
div.button-group-x:not([data-vertical]) {
  flex-direction: row;
  /* stylelint-disable-next-line no-descending-specificity */
}
div.button-group-x:not([data-vertical]) > button.button-x:not(.circle):not(.square):not(.right-angled):first-of-type:not(:last-of-type) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
div.button-group-x:not([data-vertical]) > button.button-x:not(.circle):not(.square):not(.right-angled):last-of-type:not(:first-of-type) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
div.button-group-x:not([data-vertical]) > button.button-x:not(.circle):not(.square):not(.right-angled):not(:first-of-type):not(:last-of-type) {
  border-radius: 0;
}
div.button-group-x.primary {
  background-color: #007bff;
}
div.button-group-x.secondary {
  background-color: #6c757d;
}
div.button-group-x.info {
  background-color: #17a2b8;
}
div.button-group-x.success {
  background-color: #28a745;
}
div.button-group-x.warning {
  background-color: #ffc107;
}
div.button-group-x.error {
  background-color: #dc3544;
}
div.button-group-x.dark {
  background-color: #343a40;
}
div.button-group-x.light {
  background-color: #f8f9fa;
}
